<template>
  <div>
    <!-- 子页面展示 -->
    <mt-tab-container v-model="selected" :value="selected">
      <mt-tab-container-item id="书架">
<!--        //头部-->
        <mt-header fixed :title="selected"></mt-header>
<!--        //组件-->
        <Bookrack @addBook="changeSelected" class="tab-container"></Bookrack>
      </mt-tab-container-item>
      <mt-tab-container-item id="分类">
<!--        //头部-->
        <mt-header fixed :title="selected"></mt-header>
<!--        //组件-->
        <Category class="tab-container"></Category>
      </mt-tab-container-item>
      <mt-tab-container-item id="排行">
        <!--        //头部-->
        <mt-header fixed :title="selected"></mt-header>
<!--        //组件-->
        <Rank class="tab-container"></Rank>
      </mt-tab-container-item>
      <mt-tab-container-item id="搜索">
<!--        搜索框-->
        <mt-search></mt-search>
      </mt-tab-container-item>
    </mt-tab-container>
<!--    底部tab-->
    <mt-tabbar v-model="selected" :fixed="true">
      <mt-tab-item id="书架">
        <img slot="icon" src="../assets/images/books.svg">
        书架
      </mt-tab-item>
      <mt-tab-item id="分类">
        <img slot="icon" src="../assets/images/category.svg">
        分类
      </mt-tab-item>
      <mt-tab-item id="排行">
        <img slot="icon" src="../assets/images/list.svg">
        排行
      </mt-tab-item>
      <mt-tab-item id="搜索">
        <img slot="icon" src="../assets/images/rank.svg">
        搜索
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
  import Bookrack from "../components/Bookrack"
  import Category from "../components/Category"
  import Rank from "../components/Rank"
  import Search from "../components/Search"
    export default {
      data : function () {
        return {
          selected : "书架"
        }
      },
      methods : {
        changeSelected : function (data) {
          this.selected = data;
        }
      },
      components : {
        Bookrack,
        Category,
        Rank,
        Search
      }
    }
</script>

<style scoped>
  .tab-container{
    width: 100vw;
    min-height: 100vh;
    padding:.4rem 0 .55rem 0;
    box-sizing: border-box;
    background: #fff;;
  }
</style>



