import Vue from "vue"
const baseUrl = "http://129.28.20.94";
export default {
  //获取所有分类
  getCategory : function () {
    return Vue.http.get(baseUrl + "/cats/lv2/statistics");
  },
  //获取排行榜内容
  getRank : function () {
    return Vue.http.get(baseUrl + "/ranking/gender");
  }
}



