import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import HotList from "../views/HotList";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/hotlist',
      name: 'hotlist',
      component: HotList
    }
  ]
})



