<template>
  <div>
    <section v-for="(item,key,index) in bookRank">
      <p class="bookRankTitle">{{bookRankTitle[key]}}</p>
      <ul>
        <li v-for="(item,key,index) in item" class="bookRankList">
          <img :src="'http://statics.zhuishushenqi.com/'+ item.cover">
          <span>{{item.shortTitle}}</span>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
  import api from "../api/api"
    export default {
      name: "Rank",
      data : function(){
        return {
          bookRank : "",
          bookRankTitle : {
            female : "女生",
            male : "男生",
            epub : "别人家的排行榜",
            picture : "热搜榜"
          }
        }
      },
      created() {
        var that = this;
        api.getRank().then(function (res) {
          console.log(res)
          that.bookRank = res.data;
        })
      }
    }
</script>

<style scoped>
  .bookRankTitle{
    padding: 0 .1rem;
    line-height: .3rem;
    background: papayawhip;}
  .bookRankList{
    padding: 0 .1rem;
    line-height: .35rem;}
  .bookRankList img{
    margin-right: .1rem;
    width: .24rem;
    height: .24rem;
    float: left;
    padding: .05rem 0;}
  .bookRankList span{
    display: inline-block;}
</style>



