<template>
  <div>
    <section v-for="(item,key,index) in bookcategory">
      <p class="bookTitle">{{booktitle[key]}}</p>
      <ul>
        <li v-for="(item,key,index) in item">
          <p>{{item.name}}</p>
          <span>{{item.bookCount}}</span>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
  import api from "../api/api";
  import { Indicator } from 'mint-ui';
    export default {
      name: "Category",
      data : function () {
        return {
          bookcategory : "",
          booktitle : {
            female : "男生",
            male : "女生",
            press : "出版",
            picture : "其它"
          }
        }
      },
      created() {
        var that = this;
        Indicator.open();
          api.getCategory().then(function (res) {
            that.bookcategory = res.data;
            Indicator.close();
          })
      }
    }
</script>

<style scoped>
  .bookTitle{
    padding: 0 .1rem;
    border-bottom: .01rem solid #ccc;
    line-height: .3rem;}
  .bookTitle :last-child {
    border: none;}
  ul{
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;}
  ul li{
    width: 33.3%;
    text-align: center;
    padding: .08rem 0;}
  ul li p{
    font-weight: 900;}
</style>



