  <div>
    <mt-button type="primary" class="add_book" @click="$emit('addBook','分类')">添加书籍</mt-button>
  </div>
</template>

<script>
    export default {
        name: "Bookrack"
    }
</script>

<style scoped>
  .add_book{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);}
</style>



